import { Component, Input, OnInit } from '@angular/core';

import { ActivatedRoute } from '@angular/router';
import { AuthService } from 'src/app/services/auth.service';
import { PostsServiceService } from 'src/app/services/posts-service.service';


@Component({
  selector: 'app-comments-box',
  templateUrl: './comments-box.component.html',
  styleUrls: ['./comments-box.component.scss']
})
export class CommentsBoxComponent implements OnInit {

  @Input() postId: any;
  p:number = 1;
  addComment: boolean = false;
  commentsList: any = [];
  tokenValid: boolean = this.authS.getUser();

  constructor( private activatedRoute: ActivatedRoute, private authS: AuthService, private postS: PostsServiceService) {
   
     }

  ngOnInit(): void {
    this.activatedRoute.data.subscribe(
      response => {
        this.commentsList = response.data2.comments.reverse();
      })
      
      
  }

 // Función que se encarga de mostrar y ocultar la caja de añadir-comentario según el estado de la propiedad booleana encarga de ello (this.addComment)
  changeAddComment(): void {
    this.addComment = !this.addComment;
  }


}
