import { Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import { AuthService } from 'src/app/services/auth.service';

@Component({
  selector: 'app-posts',
  templateUrl: './posts.component.html',
  styleUrls: ['./posts.component.scss']
})
export class PostsComponent implements OnInit {

  @Input() posts!: any[];
  
  @ViewChild("contenedor") content: ElementRef | any;

  p:number = 1;
  lista: any[] = [];
  //Comprobar si hay un usuario registrado
  tokenValid: boolean = this.authS.getUser();

  constructor( private authS: AuthService ) {

    
   }

  ngOnInit(): void {
    scrollTo(0,0);
   }

   ngAfterViewInit():void {
    this.mostrarcontent()
  }

   // Crear la animación de desplazamiento 
   mostrarcontent() :void {
     this.content.nativeElement.classList.remove('animado');
     this.content.nativeElement.classList.add('mostrarizquierda')
   }
   
    

}
