import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MainSectionComponent } from './components/main-section/main-section.component';
import { AddPostPageComponent } from './components/views/add-post-page/add-post-page.component';
import { ContactUsComponent } from './components/views/contact-us/contact-us.component';
import { CuentaComponent } from './components/views/cuenta/cuenta.component';
import { NotFoundComponent } from './components/views/not-found/not-found.component';
import { PostPageComponent } from './components/views/post-page/post-page.component';
import { CuentaUsuarioGuard } from './guards/cuenta-usuario.guard';
import { CommentsResolverService } from './services/resolves/comments-resolver.service';
import { OnepostresolverService } from './services/resolves/onepostresolver.service';
import { PostResolverService } from './services/resolves/post-resolver.service';

const routes: Routes = [
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  { path: 'home', component: MainSectionComponent, resolve: { data: PostResolverService} },
  { path: 'contact', component: ContactUsComponent },
  { path: 'posts/:id', component: PostPageComponent, resolve: { data: OnepostresolverService, data2: CommentsResolverService}},
  { path: 'addPost', component: AddPostPageComponent, canActivate: [CuentaUsuarioGuard]},
  { path: 'cuenta',canActivate: [CuentaUsuarioGuard] ,component: CuentaComponent},
  { path: '404', component: NotFoundComponent, pathMatch: 'full' },
  { path: '**', redirectTo: "/404", pathMatch: 'full' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
