import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { PostsServiceService } from 'src/app/services/posts-service.service';
import {  comment } from '../../../models/commentModel';


@Component({
  selector: 'app-add-comment',
  templateUrl: './add-comment.component.html',
  styleUrls: ['./add-comment.component.scss']
})
export class AddCommentComponent implements OnInit {
  @Input() postId: any;

  comment: comment;

  private userInf: any = JSON.parse(sessionStorage.getItem('uInf')!) || '';


  constructor(private fb: FormBuilder, private postS: PostsServiceService, private router: Router) {
    this.comment = new comment(this.userInf.id, this.postId, this.userInf.username, 0, 0, this.userInf.email, [], '');
   }

  ngOnInit(): void {
    this.comment.postId = this.postId;
  }

  /* Función encargada de añadir nuevo comentario a la DB 
  * @param form: recibe el formulario para hacerle un reset 
  */

  addComment(form: any) :void {
    this.postS.saveComment(this.comment).subscribe((res) => {
      this.postS.addResp(this.postId).subscribe(() => {

      })
      form.reset();
      this.router.navigateByUrl('/home', { skipLocationChange: true }).then(() => {
        this.router.navigate([`/posts/${this.postId}`]);
    }); 
    
    },(err) => {
      console.log(<any>err);
      
    });
    
  }

  
}
