import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { PostsServiceService } from 'src/app/services/posts-service.service';

@Component({
  selector: 'app-main-section',
  templateUrl: './main-section.component.html',
  styleUrls: ['./main-section.component.scss']
})
export class MainSectionComponent implements OnInit {
  @ViewChild("contenedor") content: ElementRef | any;
  categorie: string = "All";
  posts!: any[];
  constructor(private postS: PostsServiceService, private AcRoute: ActivatedRoute) { 
    
  }

  ngOnInit(): void {
    // Utilizar datos del resolver para dárselas a la propiedas 'posts'
    this.AcRoute.data.subscribe(
      response => {
        this.posts = response.data.posts;
      });    
  }
  ngAfterViewInit():void {
    this.mostrarcontent()
  }
  // Crear animaciones de desplazamiento
  mostrarcontent() {
    this.content.nativeElement.classList.remove('animado');
    this.content.nativeElement.classList.add('mostrarderecha')
  }

  //Filtrar por Categorías

  filtrarPorCat(category: string): void {

    if(category == "all"){
      this.postS.getPosts().subscribe(resp => {
        this.posts = resp.posts
      })
    } else {
      this.postS.getPostsbyCategory(category).subscribe( resp => {
      this.posts = resp.posts;
      })
    }
  }

  // Filtrar por término de busqueda;

  filtrarPorBusqueda(termino: string): void {
    this.postS.getPostsbyWord(termino).subscribe(resp => {
      this.posts = resp.posts;
    })
  }
    
}
