import { Component, EventEmitter, OnInit, Output } from '@angular/core';


@Component({
  selector: 'app-tags-box',
  templateUrl: './tags-box.component.html',
  styleUrls: ['./tags-box.component.scss']
})
export class TagsBoxComponent implements OnInit {
  @Output() onTagClick: EventEmitter<string> = new EventEmitter;

 tags: string[] = ['javascript', 'mongoDb', 'Data-Science', 'full-stack', 'css', 'grid', 'flexbox', 'http', 'back-end', 'PHP', 'angular', 'react','mySQL','bootcamp','MVC', 'nodeJs', 'express', 'metheor', 'webpack'];
  constructor() { }

  ngOnInit(): void {
  }

    /* Función encargada de emitir al elemento padre el término de búsqueda indicado 
    * @param termino: string con el contenido que se desea emitir
    */

  emitTag(termino: string): void {
    this.onTagClick.emit(termino)
  }

}
