import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { PostsServiceService } from 'src/app/services/posts-service.service';
import {  FormBuilder } from '@angular/forms';
import { post } from '../../../models/PostModel';
import {  Router } from '@angular/router';
import { MarkdownService } from 'ngx-markdown';
import { EditorInstance } from 'angular-markdown-editor';

@Component({
  selector: 'app-add-post-page',
  templateUrl: './add-post-page.component.html',
  styleUrls: ['./add-post-page.component.scss']
})
export class AddPostPageComponent implements OnInit {

  @ViewChild("container") content: ElementRef | any;

  bsEditorInstance!: EditorInstance;
  editorOptions:any;
  post: post;
  markdownText:any;
  rows:number = 12;
  private userInf: any = JSON.parse(sessionStorage.getItem('uInf')!) || '';

   
  constructor(private postS: PostsServiceService, private fb: FormBuilder, private route: Router, private markdownService: MarkdownService) { 
    this.post = new post(this.userInf.id, this.userInf.username, '',  0, 0, this.userInf.email, [], 0, '', '', 0  )
  }

  ngOnInit(): void {
    this.editorOptions = {
      parser: (val:any) => this.markdownService.compile(val.trim())
    };
  }

  ngAfterViewInit():void {
    this.mostrarcontent()
  }

  /* Función para crear nuevo post en la DB por medio de una petición http y redirecciona a la home
  * @param form: recibe un parámetro que hace referencia al formulario con el body que se envía a la petición para hacerle un reset;
  */

  addPost(form: any) :void {
    this.postS.savePost(this.post).subscribe((res) => {
      form.reset();
      window.alert('Post subido correctamente');
      this.route.navigate(['']);
      
    },(err) => {
      console.log(<any>err);
      form.reset();
    });
    
  }

  hidePreview(e:any) { console.log(e.getContent()); }

  // Función que se encarga de mostrar el contenido animado (deslizandose hacia arriba)

  mostrarcontent() :void {
    this.content.nativeElement.classList.remove('animado');
    this.content.nativeElement.classList.add('mostrarabajo')
  }

  clicks():void {
    console.log(this.post);
    
  }

}
