import { Component, OnInit, Input } from '@angular/core';
import { ConexRespService } from 'src/app/services/compConexions/conex-resp.service';

@Component({
  selector: 'app-ref-bar',
  templateUrl: './ref-bar.component.html',
  styleUrls: ['./ref-bar.component.scss']
})
export class RefBarComponent implements OnInit {

  @Input() post: any;

  num: number | string = 0;

  constructor(private conexS: ConexRespService) { }

  ngOnInit(): void {
    this.conexS.customNum.subscribe(number => this.num = number )
  }

}
