import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { PostsServiceService } from 'src/app/services/posts-service.service';

@Component({
  selector: 'app-comment',
  templateUrl: './comment.component.html',
  styleUrls: ['./comment.component.scss']
})
export class CommentComponent implements OnInit {

  get userI () {
    const info = JSON.parse(sessionStorage.getItem('uInf')!)
    if(info === null) { return { id: ''}}
    return info;
 } 

  @Input() comentario: any;

  constructor(private postS: PostsServiceService) { }

  ngOnInit(): void {
   
  }

  // Función para borrar comentario de la DB y hacer un reload de la página

  borrarComment(): void {
    this.postS.deleteComment(this.comentario._id).subscribe(resp => {
      console.log(resp);
      location.reload();
    })
  }

}
